.signin-container {
	height: 100vh;
	width: 23.5rem;
	font-size: 1rem;
	background-color: #61016e5e;
	border-left: 5px solid black;
	font-size: 1.5rem;
	font-weight: bold;

	display: flex;
	justify-content: center;
	align-items: flex-start;
}
.company-name {
	color: white;
	text-align: center;
	margin: 0 0.5rem 3rem;
	padding: 0.7rem;
	font-size: 1.8rem;
	text-transform: uppercase;
}
.Form1 {
	font-family: Verdana, Geneva, Tahoma, sans-serif;
	margin: 2.5rem 0;
	display: flex;
	flex-direction: column;
	font-weight: bold;
	color: white;
	text-shadow: 0.2rem 0.2rem 0.5rem black;
}
.label {
	font-size: 1.2rem;
	font-weight: bold;
	margin: 0.5rem 1rem;
	display: block;
}
.form_input {
	font-size: 1rem;
	padding: 0.5rem;
	border-radius: 10px;
	margin: 0 0.8rem;
	height: 2.5rem;
}

.container-btn {
	margin-top: 0.2rem;
	text-align: center;
}

.btn {
	font-size: 1rem;
	height: 2rem;
	width: 7rem;
	border-radius: 0.5rem;
	background-color: #40005d;
	color: white;
	margin: 1rem 0.7rem;
}

.btn:hover {
	background-image: linear-gradient(
		180deg,
		rgb(90, 90, 90),
		rgb(26, 26, 26),
		black
	) !important;
}
